import React from "react";
import Context from "../../../Context";
import { injectIntl } from "react-intl";
import { Card, Col, Empty, Row, Spin } from "antd";
import AdminDashLink from "../../Admin/Dashboard/AdminDashLink";
import { AppstoreOutlined, DatabaseOutlined } from "@ant-design/icons";
import Title from "../../Admin/Title";

class OcppStations extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      pending: false,
      stations: [],
    };
  }
  m = (key, prefix = "ocpp.stationlist.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  load = async () => {
    this.setState({ pending: true });
    let stations = this.context.ocpp.getArray(
      await this.context.ocpp.q("/ChargePointList", {}),
      "ChargePointList",
      "Identity"
    );

    this.setState({ pending: false, stations });
  };

  render() {
    const { pending, stations } = this.state;

    return (
      <Spin spinning={pending}>
        <Title
          title={this.m("stationlist")}
          url="/ocpp"
          icon={<AppstoreOutlined />}
        />
        {!stations.length && !pending && <Empty />}
        <Row gutter={[16, 16]}>
          {stations.map((station, idx) => {
            return (
              <Col lg={12} xs={24} key={idx}>
                <Card>
                  <Row gutter={[16, 16]} style={{ alignItems: "center" }}>
                    <Col lg={12} xs={24}>
                      <AdminDashLink
                        to={`/ocpp/command/${station?.Identity}`}
                        color="teal"
                        label={station?.Identity}
                        text={station?.Address}
                        icon={<DatabaseOutlined style={{ fontSize: "48px" }} />}
                      />
                    </Col>
                    <Col lg={12} xs={24}>
                      <h3>{station?.Connection?.IP}</h3>
                      <h4 className="gx-text-grey">
                        {station?.Connection?.Port} /
                        {station?.Connection?.Socket}
                      </h4>
                    </Col>
                  </Row>
                </Card>
              </Col>
            );
          })}
        </Row>
      </Spin>
    );
  }
}

export default injectIntl(OcppStations);
